<template>
	<div>
		<h1>2024年志愿填报</h1>
		<el-card shadow="always" class="cardFa" :body-style="{ padding: '30px' }">
			<el-form ref="form" :model="Person" label-width="70px" size="min">
				<el-form-item label="名字">
					<el-input v-model="Person.name"></el-input>
				</el-form-item>
				<el-form-item label="学号">
					<el-input v-model="Person.students"></el-input>
				</el-form-item>
				<el-form-item label="证件号">
					<el-input v-model="Person.major"></el-input>
				</el-form-item>
				<el-form-item label="查询">
					<el-select v-model="Person.indexMax" placeholder="请选择第几次">
						<el-option v-for="(data, i) in optionsData" :key="i" :label="data" :value="i"></el-option>
					</el-select>
				</el-form-item>
				<el-form-item>
					<el-button type="primary" @click="submitA">立即登陆</el-button>
					<el-button @click="zhuce">注册</el-button>
				</el-form-item>
			</el-form>
		</el-card>
	</div>
</template>

<script>
export default {
	name: "login",
	data() {
		return {
			Person: {
				name: "",
				students: "",
				major: "",
				indexMax:''
			},
			Persons: [],
			optionsData: ["第一次", "第二次", "第三次",'第四次','第五次'],
			
		};
	},
	methods: {
		// 登陆
		submitA() {

			for(let p in this.Person){
				if(['',null,undefined].includes(this.Person[p])){
					return alert(p + ':不能为空')
				}
			}
			this.$router.push({
					path: "/home",
					query: {
						name: this.Person.name,
						students: this.Person.students,
						major: this.Person.major,
						indexMax:this.Person.indexMax
					},
			        });
			
			
		},
		zhuce() {
			this.$router.push({ path: "/register" });
		},
	},
};
</script>

